import React, { Component } from 'react'


import Masonry from 'react-masonry-component';

import ATR from '../Images/Logo/All Things Rugged/atr-logo.png'
import Rentables from '../Images/Logo/RentablesPH/logo.png'
import SALogo from '../Images/Logo/SA Logo/logo.png'
import NailRoom from '../Images/Logo/The Nail Room/The-Nail-Room.png'
import WaterCare from '../Images/Logo/WaterCare PH/logo.png'

let slide = 
[
  {
    title: 'ATR',
    image: ATR,
    background: '#9d916c'
  },
   {
    title: 'The Nail Room',
    image: NailRoom,
    background: 'transparent'
  },
  {
    title: 'Rentables PH',
    image: Rentables,
    background: '#fff'
  },
  {
    title: 'SA Logo',
    image: SALogo,
    background: '#000223'
  },
 
  

]

class Home extends Component {
  constructor(props){
    super(props)
    this.state = {
        active: false
     }
     this.handleImagesLoaded = this.handleImagesLoaded.bind(this)
    
  }
  handleImagesLoaded(){
    this.setState({
      active: true
    })
  }


 
  render() {
      const masonryOptions = {
          transitionDuration: 0
      };
      const childElements = slide.map(function(attr){
           return (
              <div className='image-mason-container'>
                <div className='image-mason' style={{background: attr.background}}>
                    <img src={attr.image} />
                </div>
              </div>
            );
        });
        return (
            <Masonry
                onImagesLoaded={this.handleImagesLoaded}
                className={'my-gallery-class'} // default ''
                options={masonryOptions}   
            >
                { this.state.active ? childElements : ''}
            </Masonry>
          
        );
  }
}

export default Home
