import React, { Component } from 'react'


import Masonry from 'react-masonry-component';

import EnduinsPrototype from '../Images/UXUI Design/Enduins-WebPrototype_Homepage.jpg'
import Abra from '../Images/UXUI Design/abra.png'
import Adornata from '../Images/UXUI Design/adornata.png'


let slide = 
[
 {
    title: 'Province of Abra Official Website',
    image: Abra,
    link: 'http://abra.gov.ph/',
    background: '#e5e5e5'
  },
  {
    title: 'Adornata',
    image: Adornata,
    link: 'https://adornata.jewelry/',
    background: '#eee3d4'
  },
]

class Home extends Component {
  constructor(props){
    super(props)
    this.state = {
        active: false
     }
     this.handleImagesLoaded = this.handleImagesLoaded.bind(this)
    
  }
  handleImagesLoaded(){
    this.setState({
      active: true
    })
  }
 
  render() {
    const masonryOptions = {
          transitionDuration: 0
      };
      const childElements = slide.map(function(attr){
           return (
              <div className='image-mason-container'>
                <a href={attr.link} target='_blank'>
                  <div className='image-mason' style={{background: attr.background}}>
                      <img src={attr.image} />
                  </div>
                </a>
              </div>
            );
        });
        return (
            <Masonry
                onImagesLoaded={this.handleImagesLoaded}
                className={'my-gallery-class'} // default ''
                options={masonryOptions}   

            >
                { this.state.active ? childElements : ''}
            </Masonry>
          
        );
  }
}

export default Home
